<template>
  <div
    id="selfPassenger"
    v-loading="loading"
    element-loading-background="#f8f8f8"
  >
    <el-row>
      <h2>乘客</h2>
      <el-divider></el-divider>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
    };
  },
  activated() {
    console.log("activated");
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfPassenger {
  padding: 30px 25px;
}
</style>
